import React, { useState } from 'react';
import { RxHamburgerMenu } from "react-icons/rx";
import { RiLogoutCircleRLine } from "react-icons/ri";
import { CgProfile } from "react-icons/cg";
import { Link, useHistory } from 'react-router-dom';
import './Sidebar.css';

const AdminHeader = ({ onToggleSidebar, sidebarCollapsed, username, userProfileImage }) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const history = useHistory();

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const handleLogout = () => {
    // Perform logout logic here
    localStorage.removeItem("authToken");
    window.location.reload();
    window.location.href = "/admin/login";
  };

  const handleSidebarToggle = () => {
    onToggleSidebar(); // Toggle the sidebar first
    setIsMenuOpen(false); // Close the menu dropdown when sidebar is toggled
  };

  return (
    <header className="admin-header">
      
      <div className="header-left">
        <Link to="#" className="logo-link">
          {/* Your logo or brand */}
        </Link>
        <button className="c-nav-btn" onClick={handleSidebarToggle}>
          <RxHamburgerMenu />
        </button>
      </div>
      <div className="header-right">
        <div className="header-info2 d-flex align-items-center" onClick={toggleMenu}>
          <div className="d-flex align-items-center sidebar-info">
            <div>
              <h4 className="mb-0">Nella Vita</h4>
              <span className="d-block text-end">Admin</span>
            </div>
          </div>
          {/* <img src={require('../imgs/single-service-1.jpg').default}></img> */}

          <img src={require('../imgs/user.jpg').default} alt="" className='header-profile' />
        </div>
        {isMenuOpen && (
          <ul className="menu-dropdown">
            <li><Link to="#"><span><CgProfile /></span>My Profile</Link></li>
            <li><button onClick={handleLogout}><span><RiLogoutCircleRLine /></span>Logout</button></li>
          </ul>
        )}
      </div>

    </header>
  );
};

export default AdminHeader;