import React, { useState } from "react";
import { FaAward } from "react-icons/fa";
import { FaUserDoctor } from "react-icons/fa6";
import { AiTwotoneDashboard } from "react-icons/ai";
import { BrowserRouter as Router, Switch, Route, Redirect } from "react-router-dom";
import { Link } from 'react-router-dom';
import "./Sidebar.css";
import AdminHeader from "./AdminHeader";
import  Managespecialties from "./Managespecialties"
import { FaBlog } from "react-icons/fa";
import { IoIosContact } from "react-icons/io";
import { IoHomeSharp } from "react-icons/io5";
import { BiSolidCategory } from "react-icons/bi";
import { FaPhotoVideo } from "react-icons/fa";
import VideoUpload from "./VideoUpload";
import Photos from "./Photo";
import AdminPhoto from "./Adminpoto";
import AdminHomePage from "../routing/Adminhome";
import AdminAboutUsPage from "./AdminAboutUsPage";
import Adminabout from "./Adminabout";
import Pagescategory from "./Pagescategory";
import PageSubCategory from "./PageSubCategory";
import ContentSection from "./ContentSection";
import AddDoctorForm from "./AddDoctorForm";
import OurDoctors from "./Ourdoctors";
import Managedoctors from "./Managedoctors";
import DoctorsPage from "./Managedoctors";
import Managetpa from "./Managetpa";
import AddTpa from "./AddTpa";
import EditTpa from "./Edittpa";
import Manageblog from "./Manageblog";
import AddBlog from "./Addblog";
import AddSpecialties from "./Addspecialties";
import ManageCardiology from "./ManageCardiology ";
import AddCardiology from "./AddCardiology";
import ManageGyna from "./ManageGyna";
import AddGyna from "./Addgyna";
import ManageGALS from "./Managegals";
import AddGALS from "./Addgals";
import ManageGM from "./ManageGM";
import AddGM from "./AddGM";
import AddContent from "./AddContent";
import ManagePages from "./ManagePages";
import ManageContent from "./ManageContent";
import Addabout from "./Addabout";
import ManageAbout from "./Manageabout";
import AddPageCategory from "./AddPageCategory ";
import Managemedia from "./Managemedia";
import AddMedia from "./AddMedia";
import ManageOPD from "./Manageopd";
import AddOPD from "./AddOPD";

const Sidebar = () => {
  const [isMediaDropdownOpen, setIsMediaDropdownOpen] = useState(false);
  const [isPagesOpen, setIsPagesOpen] = useState(false);

  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const [isSpecialtiesOpen, setIsSpecialtiesOpen] = useState(false);
  // const [isPagesOpen, setIsPagesOpen] = useState(false);
  const [currentPage, setCurrentPage] = useState("Home");
  const [currentPath, setCurrentPath] = useState("Home");

  const toggleMediaDropdown = () => {
    setIsMediaDropdownOpen(!isMediaDropdownOpen);
  };

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen);
  };

  const toggleSpecialties = () => {
    setIsSpecialtiesOpen(!isSpecialtiesOpen);
  };

  const togglePages = () => {
    setIsPagesOpen(!isPagesOpen);
  };

  const toggleSidebar = () => {
    document.getElementById("sidebar").classList.toggle("expand");
  };

  const handlePageChange = (pageName) => {
    setCurrentPage(pageName);
    setCurrentPath(pageName);
  };

  const handleSubPageChange = (subPageName) => {
    setCurrentPath(`${currentPage} / ${subPageName}`);
    
    if (subPageName === "Photos") {
      setIsMediaDropdownOpen(false);
    } else if (subPageName === "Videos") {
      setIsMediaDropdownOpen(false);
    } else if (subPageName === "Cardiology" || subPageName === "Nutrition" || subPageName === "ENT" || subPageName === "Medicine") {
      setIsDropdownOpen(false);
    }
  };
  

  return (
    <>
      <Router>
        <div className="wrapper">
          <aside id="sidebar" className="expand">
            <div className="logo">
              {/* <button className="toggle-btn" type="button" onClick={toggleSidebar}> */}
                <img className="logo-light" src={require('../imgs/logo.png').default} alt="logo" />
              {/* </button> */}
              <div className="sidebar-logo">
                <Link to="#"><span style={{fontSize:"25px", fontWeight:700}}>PHMC</span></Link>
              </div>
            </div>
            <ul className="sidebar-nav">
            <li className="sidebar-item">
                <Link to="/admin/dashboard" className="sidebar-link" onClick={() => handlePageChange("Home")}>
                <AiTwotoneDashboard />
                  <span className="span">Dashboard</span>
                </Link>
              </li>
              {/* <li className="sidebar-item">
                <Link to="/manage-about" className="sidebar-link" onClick={() => handlePageChange("About")}>
                <IoHomeSharp />
                  <span className="span">Manage About</span>
                </Link>
              </li> */}
<li className="sidebar-item">
  <Link
    to="#"
    className={`sidebar-link ${isDropdownOpen ? "collapsed" : ""}`}
    onClick={() => {
      toggleDropdown();
      handlePageChange("Category");
    }}
  >
    <BiSolidCategory />
    <span>Manage Pages</span>
    <span className="dropdown-arrow"></span>
  </Link>
  <ul className={`sidebar-dropdown list-unstyled collapse ${isDropdownOpen ? "show" : ""}`}>
    <li className="sidebar-item">
      <Link to="/admin/pagescategory" className="sidebar-link" onClick={() => handleSubPageChange("Pagescategory")}>
        Page Category
      </Link>
    </li>    
    <li className="sidebar-item">
      <Link to="/admin/pagesubcategory" className="sidebar-link" onClick={() => handleSubPageChange("About")}>
        Page Sub Category
      </Link>
    </li>
    <li className="sidebar-item">
      <Link to="/admin/manage-content" className="sidebar-link" onClick={() => handleSubPageChange("ContentSection")}>
        Manage Specialties Contents
      </Link>
    </li>
  </ul>
</li>

              
                    {/* </ul> */}
                  {/* </li> */}
                {/* </ul> */}
                
              {/* </li> */}

              {/* <li className="sidebar-item">
                <Link
                  to="#"
                  className={`sidebar-link ${isMediaDropdownOpen ? "collapsed" : ""}`}
                  onClick={() => {
                    toggleMediaDropdown();
                    handlePageChange("Media");
                  }}
                >
                  <FaPhotoVideo />
                  <span>Media</span>
                  <span className="dropdown-arrow"></span>
                </Link>
                <ul className={`sidebar-dropdown list-unstyled collapse ${isMediaDropdownOpen ? "show" : ""}`}>
                  <li className="sidebar-item">
                    <Link to="/admin/adminphotos" className="sidebar-link" onClick={() => handleSubPageChange("Photos")}>
                      Photos
                    </Link>
                  </li>
                  <li className="sidebar-item">
                    <Link to="/admin/video" className="sidebar-link" onClick={() => handleSubPageChange("Videos")}>
                      Videos
                    </Link>
                  </li>
                </ul>
              </li> */}

              
              <li className="sidebar-item">
                <Link to="/admin/manage-media" className="sidebar-link" onClick={() => handlePageChange("Managemedia")}>
                <FaPhotoVideo />
                  <span>Media</span>
                </Link>
              </li>

              
              <li className="sidebar-item">
                <Link to="/admin/manage-doctors" className="sidebar-link" onClick={() => handlePageChange("Managedoctors")}>
                <FaUserDoctor />
                  <span>Manage Doctors</span>
                </Link>
              </li>

              <li className="sidebar-item">
                <Link to="/admin/manage-tpa" className="sidebar-link" onClick={() => handlePageChange("Managetpa")}>
                <BiSolidCategory />
                  <span>Manage TPA</span>
                </Link>
              </li>

              <li className="sidebar-item">
                <Link to="/admin/manage-blog" className="sidebar-link" onClick={() => handlePageChange("News/Blog")}>
                  <FaBlog />
                  <span>Manage Blog</span>
                </Link>
              </li>

              
              <li className="sidebar-item">
                <Link to="/admin/manage-specialties" className="sidebar-link" onClick={() => handlePageChange("News/Blog")}>
                  <FaAward />
                  <span>Manage Specialties</span>
                </Link>
              </li>

              
              <li className="sidebar-item">
                <Link to="/admin/manage-opd" className="sidebar-link" onClick={() => handlePageChange("News/Blog")}>
                  <FaAward />
                  <span>Manage OPD</span>
                </Link>
              </li>
            </ul>
            <div className="sidebar-footer">
              <Link to="#" className="sidebar-link">
                <i className="lni lni-exit"></i>
                <span>Logout</span>
              </Link>
            </div>
          </aside>
          <div className="main">
          <AdminHeader onToggleSidebar={toggleSidebar} />
            <div className="text-center p-4">
              <hr className="header-divider" />
              {/* <div className="header-bottom">{currentPath}</div> */}
              <Switch>
  {/* Check if the current route matches /dashboard */}
  <Route exact path="/admin/dashboard">
    <AdminHomePage />
  </Route>
  {/* Render other routes */}
  {/* <Route path="/admin/video" component={VideoUpload} /> */}
  <Route path="/admin/manage-about" component={ManageAbout} />
  <Route path="/admin/manage-opd" component={ManageOPD} />
  <Route path="/admin/manage-media" component={Managemedia} />
  <Route path="/admin/manage-content" component={ManageContent} />
  <Route path="/admin/manage-gm" component={ManageGM} />
  <Route path="/admin/manage-cardiology" component={ManageCardiology} />
  <Route path="/admin/manage-gals" component={ManageGALS} />
  <Route path="/admin/manage-pages" component={ManagePages} />
  <Route path="/admin/manage-gyna" component={ManageGyna} />
  <Route path="/admin/manage-blog" component={Manageblog} />
  <Route path="/admin/manage-specialties" component={Managespecialties} />
  <Route path="/admin/edit-tpa" component={EditTpa} />
  <Route path="/admin/add-gals" component={AddGALS} />
  <Route path="/admin/add-opd" component={AddOPD} />
  <Route path="/admin/add-media" component={AddMedia} />
  <Route path="/admin/add-about" component={Addabout} />
  <Route path="/admin/add-content" component={AddContent} />
  <Route path="/admin/add-gm" component={AddGM} />
  <Route path="/admin/add-pagecategory" component={AddPageCategory} />
  <Route path="/admin/add-tpa" component={AddTpa} />
  <Route path="/admin/add-gyna" component={AddGyna} />
  <Route path="/admin/add-cardiology" component={AddCardiology} />
  <Route path="/admin/add-specialties" component={AddSpecialties} />
  <Route path="/admin/add-blog" component={AddBlog} />
  <Route path="/admin/manage-tpa" component={Managetpa} />
  <Route path="/admin/add-doctor" component={AddDoctorForm} />
  <Route path="/admin/manage-doctors" component={DoctorsPage} />
  <Route path="/admin/contentsection" component={ContentSection} />
  <Route path="/admin/pagesubcategory" component={PageSubCategory} />
  <Route path="/admin/pagescategory" component={Pagescategory} />
  <Route path="/about" component={Adminabout} />
  <Route path="/adminabout" component={AdminAboutUsPage} />
  <Route path="/adminphotos" component={AdminPhoto} />
  {/* Add other routes here */}
</Switch>
            </div>
          </div>
        </div>
      </Router>
    </>
  );
};

export default Sidebar;